.movies .movie {
    width: 45%;
    background-color: #222;
    margin-top: 50px;
    margin-bottom: 80px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
}
.movies .movie a {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-decoration: none;
    color: inherit;
  }

.movies .movie:nth-of-type(even) {
    margin-top: 300px;
}



.movie img {
    position: relative;
    top: -50px;
    max-width: 50%;
    width: 100%;
    margin: 0 auto;
    transition: 0.5s;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie img:hover {
    transform: translateY(-30px);
}

.info {
    display: flex;
    flex-wrap: wrap;

    align-items: center;
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
}

.movie .movie__year {
    font-weight: bold;
}

.movie .movie__title {
    margin-bottom: 5px;
    margin-right: 10px;
    font-size: 24px;
    color: white;
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}

.movie__genres li,
.movie .movie__year {
    margin-right: 10px;
    font-size: 14px;
}

.movie__summary {
    flex-basis: 100%;
}