* {
    box-sizing: border-box;
  }
  body {
    margin: 0;
    padding: 0;
    background-color: #111;
    height: 100%;
  }
  html,
  body,
  #potato,
  .container {
    height: 100%;
    display: flex;
    justify-content: center;
  }
  .loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: #f2f2f2;
    font-size: 2rem;
  }
  .top{
      text-align: center;
      width: 100%;
      height: 100px;
      color: #f2f2f2;
      font-size: 3rem;
  }
  .movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%;
  }